<template>
  <div class="straightBasket">
    <v-container
      fluid
      grid-list-xl>
      <!-- 語系選單(需隱藏) -->
      <SelectSelectLanguage v-show="false"/>
      <v-layout
        row
        wrap
        class="p-0 my-0">
        <v-flex
          md6
          xs12
          tag="h3"
          class="my-0 pb-3">
          {{ $ln(gname) }}-{{ $ln('單式入球數') }}
        </v-flex>

        <SportBallStatus
          :dataSelect.sync="selectChangeItems.selected"
          :items="selectChangeItems.options"
          title="拆帳"
        />
        <!-- 每秒更新 -->
        <CommonCounter
          :reciprocalItems.sync="reciprocalItems"
          :updateTableList="updateTableList"
        />
      </v-layout>

      <div>
        {{ $ln('帳務時間') }}：{{ count_date }} {{ $ln('球隊') }}：{{ main_team }}(H)VS {{ visit_team }}
      </div>
      {{ league_name }}-{{ transType }}

      <div v-if="this.$route.query.transType === '全場'" class="pt-1">
        <div class="rwdTable no-rwd-table">
          <div class="v-table__overflow">
            <table class="v-datatable v-table theme--light goals">
              <thead>
                <tr>
                    <th v-for="(header, i) in goalsListAll.headers" :key="i" :colspan="header.colspan">{{ $ln(header.text) }}</th>
                  </tr>
                  <tr>
                    <th v-for="(header, i) in goalsListAll.headers2" :key="i">{{ $ln(header.text) }}</th>
                  </tr>
              </thead>
              <tbody v-if="goalsListAll.List.length > 0">
                <tr v-for="(item, i) in goalsListAll.List" :key="i">
                  <td class="width25 report-bills-white-space">
                    {{ item.text }}
                  </td>
                  <td class="width25 report-bills-white-space">
                    {{ item.text2 }}
                  </td>
                  <td class="width25 report-bills-white-space">
                    {{ item.text3 }}
                  </td>
                  <td class="width25 report-bills-white-space">
                    {{ item.text4 }}
                  </td>
                </tr>
              </tbody>
              <tbody v-else>
                <tr><td colspan="4" align="center">{{ goalsList.showNoData }}</td></tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>

      <div v-if="this.$route.query.transType ==='上半場'" class="pt-1">
        <div class="rwdTable no-rwd-table">
          <div class="v-table__overflow">
            <table class="v-datatable v-table theme--light goals">
              <thead>
                <tr>
                    <th v-for="(header, i) in goalsList.headers" :key="i" :colspan="header.colspan">{{ $ln(header.text) }}</th>
                  </tr>
                  <tr>
                    <th v-for="(header, i) in goalsList.headers2" :key="i">{{ $ln(header.text) }}</th>
                  </tr>
              </thead>
              <tbody v-if="goalsList.List.length > 0">
                <tr v-for="(item, i) in goalsList.List" :key="i">
                  <td class="width25 report-bills-white-space">
                    {{ item.text }}
                  </td>
                  <td class="width25 report-bills-white-space">
                    {{ item.text2 }}
                  </td>
                  <td class="width25 report-bills-white-space">
                    {{ item.text3 }}
                  </td>
                  <td class="width25 report-bills-white-space">
                    {{ item.text4 }}
                  </td>
                  <td class="width25 report-bills-white-space">
                    {{ item.text5 }}
                  </td>
                  <td class="width25 report-bills-white-space">
                    {{ item.text6 }}
                  </td>
                </tr>
              </tbody>
              <tbody v-else>
                <tr><td colspan="6" align="center">{{ goalsList.showNoData }}</td></tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import $ from 'jquery'
import { common } from '@/utils'
import _game from "@/utils/api/game"

export default {
  name: 'SoccerGoals',
  data () {
    return {
      gname: '足球',
      goalsListAll: {
        show: true,
        showNoData: '目前無資料',
        headers: [
          { text: '全場',colspan: 4, show: true, align: 'center', sortable: false },
        ],
        headers2: [
          { text: '0-1', value: 'g01',  show: true, align: 'center', sortable: false, class:'width25' },
          { text: '2-3', value: 'g23', show: true, align: 'center', sortable: false, class:'width25' },
          { text: '4-6', value: 'g46', show: true, align: 'center', sortable: false, class:'width25' },
          { text: '7或以上', value: 'g7',  show: true, align: 'center', sortable: false, class:'width25' }
        ],
        
        List: [
          { text: '11', text2: '11', text3: '11', text4: '11'}
        ]
      },
      goalsList: {
        show: true,
        showNoData: '目前無資料',
        headers: [
          { text: '上半場',colspan: 6, show: true, align: 'center', sortable: false },
        ],
        headers2: [
          { text: '0', value: 'g0',  show: true, align: 'center', sortable: false },
          { text: '1', value: 'g1',  show: true, align: 'center', sortable: false },
          { text: '2', value: 'g2',  show: true, align: 'center', sortable: false },
          { text: '3', value: 'g3',  show: true, align: 'center', sortable: false },
          { text: '3或以上', value: 'g4',  show: true, align: 'center', sortable: false },
          { text: '4或以上', value: 'g5',  show: true, align: 'center', sortable: false },
        ],
        
        List: [
          { text: '11', text2: '11', text3: '11', text4: '11',text5: '11', text6: '11'}
        ]
      },
      // 拆帳選擇
      selectChangeItems: {
        selected: 1,
        options: [
          { text: '100％', value: 1 },
          { text: '自己', value: 0 }
        ]
      },
      
      // 秒數倒數更新
      reciprocalItems: {
        selected: '',
        options: [
          { text: '每180秒更新', value: 180 },
          { text: '每60秒更新', value: 60 },
          { text: '每5秒更新', value: 5 },
          { text: '不更新', value: '' }
        ],
        // 倒數 timer
        updateListTimer: null,
        // 當前秒數
        currentSpeed: '',
        // 確定更新秒數
        confirmSpeed: ''
      },
      dataList: [],
      game_time: '',
      occupy: '',
      count_date: '',
      main_team: '',
      visit_team: '',
      league_name: '',
      transType: '',
      posting: '',
      game_serial_number: '',
      game_type: ''
    }
  },
  computed: {
  },
  watch: {
    // 每秒更新切換
    'reciprocalItems.selected' (value) {
      this.updateTableList()
    },
    'reciprocalItems.currentSpeed' (value) {
      // 倒數為0秒時刷新列表
      if (value === 0) {
        this.getPlayTypeDetail()
      }
    },
    // (拆帳)切換
    'selectChangeItems.selected' (value) {
      // this.occupy = value
      // console.log('occupy',value)
      this.getPlayTypeDetail(value)
    },
    'selectTimeItems.selected' (value) {
      clearInterval(this.interval)
      this.selectedTimer = value
      if (value === 0) {
        this.intervalTimer = 0
      } else if (value === 0 || value === 5 || value === 60 || value === 180) {
        this.intervalTimer = 0
        this.setIntervalTime()
      }
    }
  },
  mounted () {
    this.getPlayTypeDetail()
  },
  methods: {
    /* 載入資料
     * @param occupy
     */
    async getPlayTypeDetail (occupy) {
      // console.log('>>>', occupy)
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        occupy:  occupy !== undefined ? occupy : this.$route.query.occupy,
        posting: this.$route.query.Posting,
        game_serial_number: this.$route.query.gameSN,
        game_type: this.$route.query.game_type,
        play_type: 'goal'
      }
      console.log('params >>>', params)
      let res = await _game.getPlayTypeDetail(params)
      this.main_team = res.main_team
      this.visit_team = res.visit_team
      this.count_date = res.count_date
      this.game_time = res.game_time
      this.league_name = res.league_name
      this.transType = res.transType
      
    },
    /**
     * 秒數倒數
     * @params  [speed]  倒數秒數
     **/
    startCountdown (speed) {
      this.reciprocalItems.currentSpeed = speed
      this.reciprocalItems.updateListTimer = setInterval(() => {
        this.reciprocalItems.currentSpeed--
        // 秒數為負數，則重新倒數
        if (this.reciprocalItems.currentSpeed < 0) {
          // 清除 timer
          clearInterval(this.reciprocalItems.updateListTimer)
          // 重新倒數秒數
          this.startCountdown(this.reciprocalItems.confirmSpeed)
        }
      }, 1000)
    },
    // 啟動秒數倒數刷新表格資料
    updateTableList () {
      this.getPlayTypeDetail()

      // 確定更新秒數
      this.reciprocalItems.confirmSpeed = this.reciprocalItems.selected

      // 清除 timer
      clearInterval(this.reciprocalItems.updateListTimer)

      // 秒數為負數，則不更新倒數功能
      if (this.reciprocalItems.selected < 0) {
        // 當前秒數設置空值
        this.reciprocalItems.currentSpeed = ''
        return
      }
      // 倒數秒數
      this.startCountdown(this.reciprocalItems.confirmSpeed)
    }
  }
}
</script>
<style scoped>
.goals {
    max-width: 40%;
}
.width25 {
    width: 25%;
}
</style>